/* ================================
      Question Card Container
================================ */
.question-card {
  background: white;
  padding: 32px;
  border-radius: 18px;
  box-shadow: 0 10px 25px rgba(0,0,0,0.08);
  margin-top: 20px;
  animation: fadeIn 0.4s ease-out;
}

/* Fade animation */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}


/* ================================
      Header area
================================ */
.chapter-title {
  font-size: 18px;
  font-weight: 700;
  color: #444;
  display: flex;
  gap: 6px;
}

.chapter-number {
  color: #6c63ff;
}

.section-progress {
  background: #f0ecff;
  padding: 6px 12px;
  border-radius: 10px;
  color: #6c63ff;
  font-weight: 600;
}


.q-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 18px;
}

.q-section {
  padding: 6px 14px;
  border-radius: 10px;
  background: rgba(108, 99, 255, 0.15);
  color: #6c63ff;
  font-weight: 600;
}

.q-progress-group {
  display: flex;
  gap: 12px;
}

.q-progress-chapter,
.q-progress-overall {
  background: #f7f7f7;
  padding: 6px 12px;
  border-radius: 10px;
  font-size: 14px;
  color: #777;
}


/* ================================
      Question text
================================ */
.q-text {
  font-size: 22px;
  font-weight: 700;
  margin: 18px 0 22px 0;
  color: #333;
}


/* ================================
      Options Buttons
================================ */
.q-options {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.q-option {
  background: #f7f7fc;
  padding: 14px 18px;
  border-radius: 12px;
  border: 2px solid transparent;
  font-size: 17px;
  cursor: pointer;
  transition: 0.2s;
}

.q-option:hover {
  background: #ececff;
}

.q-option.selected {
  background: #6c63ff;
  color: white;
  border-color: #5148e2;
}


/* ================================
      Textarea (Ï£ºÍ¥ÄÏãù)
================================ */
.q-textarea {
  width: 100%;
  min-height: 120px;
  font-size: 16px;
  padding: 14px;
  border-radius: 12px;
  border: 2px solid #ddd;
  outline: none;
  transition: 0.2s;
  resize: vertical;
}

.q-textarea:focus {
  border-color: #6c63ff;
  box-shadow: 0 0 8px rgba(108, 99, 255, 0.4);
}


/* ================================
      Footer Buttons Layout
================================ */
.q-footer {
  margin-top: 28px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}


/* ================================
      Unified Button Style (Next & Back)
================================ */
.q-btn {
  padding: 12px 26px;
  min-width: 130px;
  height: 48px;
  border-radius: 12px;
  font-size: 17px;
  font-weight: 600;
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: 0.22s ease;
  border: none;
}


/* üî• Next button ‚Äî gradient, glow, premium style */
.next-btn {
  background: linear-gradient(135deg, #6c63ff, #857cff);
  color: white;
  box-shadow: 0 4px 12px rgba(108, 99, 255, 0.35);
}

.next-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 18px rgba(108, 99, 255, 0.55);
}

.next-btn:disabled,
.next-btn:not(.active) {
  background: #ddd;
  color: #999;
  box-shadow: none;
  cursor: not-allowed;
}


/* üüß Back button ‚Äî transparent orange glow */
.back-btn {
  background: rgba(255, 159, 67, 0.25);
  border: 2px solid rgba(255, 159, 67, 0.55);
  color: #ff9f43;
  margin-right: auto;
  box-shadow: 0 4px 12px rgba(255, 159, 67, 0.25);
}

.back-btn:hover {
  background: rgba(255, 159, 67, 0.35);
  border-color: rgba(255, 159, 67, 0.85);
  transform: translateY(-3px);
  box-shadow: 0 6px 18px rgba(255, 159, 67, 0.45);
}


/* ================================
      Mobile responsive
================================ */
@media (max-width: 480px) {
  .q-text {
    font-size: 19px;
  }

  .q-btn {
    width: 100%;
    min-width: unset;
  }

  .q-footer {
    flex-direction: column;
    gap: 12px;
  }

  .back-btn {
    width: 100%;
  }

  .next-btn {
    width: 100%;
  }
}
